/* See https://github.com/twbs/bootstrap/blob/v4-dev/scss/_variables.scss */

$body-color: rgba(0, 0, 0, 0.87);
$body-bg: #f1f1f1;

$danger: #e9374a;
$success: #30c386;
$info: #bd10e0;
$muted: rgba(0, 0, 0, .4);
$secondary: #7f7f7f;
$light: $body-bg;

$font-family-base: Roboto, Arial, sans-serif !default;
$h1-font-size: 1.5rem; // 24px
$h2-font-size: 1.25rem; // 20px
$h3-font-size: 1rem; // 16px
$h4-font-size: 0.875rem; // 14px
$headings-line-height: 1.15 !default;
$headings-font-weight: 400 !default;
$headings-margin-bottom: 0.9375rem !default; // 15px
$paragraph-margin-bottom: 0.9375rem; // 15px
$small-font-size: 0.75rem !important; // 12px
$line-height-base: 1.25 !default;

$font-size-lg: 1rem !default;

$border-radius: 3px;
$border-color: rgba(0, 0, 0, 0.1) !default;

$input-bg: transparent !default;
$input-border-color: $border-color;
$input-border-radius: 0;
$input-padding-x: 0;
$input-placeholder-color: rgba(0, 0, 0, 0.38);
$input-plaintext-color: $body-color;
$custom-select-border-color: $input-border-color;
$custom-select-border-radius: 0;
$custom-select-padding-x: 0;
$custom-select-bg: transparent !default;
$input-focus-border-color: $body-color;
$form-group-margin-bottom: 1.875rem; // 30px
$custom-control-indicator-checked-color: rgba(0, 0, 0, 0.54);

$btn-padding-y: .325rem;
$btn-font-weight: 500;
$btn-line-height: 1.575;
$btn-padding-y-lg: .695rem;

$alert-border-radius: 0;
$alert-padding-x: 0;
$alert-margin-bottom: 0;
$alert-border-width: 0;

$badge-font-size: 0.875rem;
$badge-font-weight: 400;
$badge-padding-x: 10px;
$badge-pill-padding-x: 10px;
$badge-padding-y: 4.5px;

$link-hover-decoration: none;

$nav-link-padding-y: 1rem; // 16px

$nav-tabs-border-width: 0;
$nav-tabs-border-radius: 0;
$nav-tabs-link-active-color: $body-color;
$nav-tabs-link-active-bg: transparent;
$nav-tabs-link-active-border-color: $body-color;

$spacer: 1.25rem !default;
$spacers: (0: 0, 1: $spacer * .25, 2: $spacer * .5, 3: $spacer * .75, 4: $spacer, 5: $spacer * 1.5, 6: $spacer * 3) !default;

@import url('https://fonts.googleapis.com/css?family=Roboto:200,300,400,400i,500,500i,700,700i&subset=cyrillic-ext');
@import '~bootstrap/scss/bootstrap-reboot';

* {
  &, &::before, &::after {
    transition-property: background-color, border, margin, padding, left, right;
    transition-duration: .15s;
    transition-timing-function: ease-in-out;
  }
}

h1 {
  line-height: 1.08;
}

small, .small {
  line-height: 1rem;
}

.strong {
  font-weight: 500;
}

.text-uppercase {
  line-height: 1;
}

.afake {
  color: $link-color;
  text-decoration: $link-decoration;
  background-color: transparent; // Remove the gray background on active links in IE 10.
  -webkit-text-decoration-skip: objects; // Remove gaps in links underline in iOS 8+ and Safari 8+.
  cursor: pointer;

  @include hover {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }
}

a, .afake {
  transition-property: color, opacity;
  transition-duration: .15s;
  transition-timing-function: ease-out;

  &.text-dark:hover,
  &.text-info:hover,
  &.text-danger:hover {
    opacity: .8;
  }

  img {
    transition: opacity .15s ease-out;
  }

  &:hover {
    img {
      opacity: .8;
    }
  }
}

.text-uppercase {
  letter-spacing: 0.9px;
}

.btn {
  text-transform: uppercase;
  outline: none !important;
  box-shadow: none !important;
}

.btn-square {
  width: 37.2px;
}

.form-group {
  position: relative;

  .icon {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;

    &[data-title="help"] {
      opacity: .4;
    }
  }
}

.form-control {
  outline: none !important;
  box-shadow: none !important;

  &:not(textarea) {
    border-top: none;
    border-right: none;
    border-left: none;
  }

  &:focus,
  &:active {
    border-color: #757575;
  }

  &[readonly] {
    background: $input-bg;
    pointer-events: none;
  }
}

.form-control-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  padding: 7px 0 0;
  transition: all 200ms;
  color: $input-placeholder-color;
  z-index: 0;
}

.form-control:focus + .form-control-placeholder,
.form-control:valid + .form-control-placeholder {
  font-size: $h4-font-size;
  transform: translate3d(0, -100%, 0);
  top: 1px;
  opacity: 1;
}

textarea.form-control {
  border-radius: 2px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.custom-select {
  background-image: url('data:image/svg+xml;base64,') !important;
  background-size: 24px !important;
  background-position: 100%;
  background-repeat: no-repeat;

  &-with-placeholder {
    &, &:focus {
      color: $input-placeholder-color !important;
    }

    option:not([disabled]) {
      color: $input-plaintext-color;
    }
  }
}

.is-invalid {
  .form-control {
    border-color: $danger;
  }

  .custom-control-label {
    &::before {
      border-color: $danger !important;
      background-color: $white !important;
    }
  }
}


.list-unstyled {
  margin-bottom: 0;
}

.list-hover {
  li {
    color: $body-color;

    @include hover {
      color: $body-color;
      background-color: rgba(0, 0, 0, 0.03);
    }

    a, .afake {
      color: inherit;
    }
  }

  .container & {
    li {
      position: relative;

      &::before {
        position: absolute;
        top: 0;
        right: -15px;
        bottom: 0;
        left: -15px;
        content: '';
        background-color: inherit;
        transition: background-color .15s ease;
        z-index: -1;

        @media (min-width: map-get($grid-breakpoints, md)) {
          right: -40px;
          left: -40px;;
        }
      }

      &:hover {
        background-color: inherit;

        &::before {
          background-color: rgba(0, 0, 0, 0.03);
        }
      }
    }
  }
}

.badge-s {
  font-size: 0.5625rem !important;
  padding: 1.5px 5.5px !important;
}

.nav-tabs {
  .nav-item {
    flex: 1;
    text-align: center;
  }

  .nav-link {
    color: $body-color;

    &.active {
      border-bottom: 2px solid $body-color;
      color: $body-color;
      font-weight: 500;
    }
  }
}

.custom-checkbox {
  position: relative;
  width: 40px;
  height: 24px;
  margin-right: 0 !important;
  padding-right: 0 !important;
  padding-left: 0 !important;
  top: 7px;

  .custom-control-label {
    cursor: pointer;

    &::before {
      position: relative;
      width: 34px;
      height: 14px;
      min-height: 0;
      margin: 3px 0 0 2px;
      border-radius: 13px !important;
      background-color: rgba(34, 31, 31, 0.26);
      box-shadow: none !important;
      opacity: 1;
    }

    &::after {
      width: 20px;
      height: 20px;
      right: calc(100% - 20px);
      left: auto;
      border-radius: 50%;
      background-color: $white;
      box-shadow: inset .5px .5px .5px rgba(0, 0, 0, 0.04), .5px .5px .5px rgba(0, 0, 0, 0.24), 0 1px 1px 1px rgba(0, 0, 0, 0.12);
    }
  }

  .custom-control-input:checked + .custom-control-label {
    &::before {
      background-color: $success !important;
      opacity: .5;
    }

    &::after {
      right: 0;
      background-image: none !important;
      background-color: $success;
      box-shadow: inset .5px .5px .5px rgba(0, 0, 0, 0.04), 0 2px 4px 0 rgba(36, 114, 82, .2);
    }
  }

  .custom-control-input:active + .custom-control-label {
    &::before {
      background-color: $success !important;
      opacity: .4;
    }
  }
}

.custom-radio {
  margin-right: 0 !important;
  padding-right: 0 !important;
  padding-left: 35px !important;

  .custom-control-label {
    cursor: pointer;

    &::before {
      width: 20px;
      height: 20px;
      top: 0;
      left: 0;
      background-color: $white;
      border: 2px solid $secondary;
      box-shadow: none !important;
    }

    &::after {
      width: 9px;
      height: 9px;
      top: 0;
      bottom: calc(100% - 20px);
      left: 0;
      right: calc(100% - 20px);
      margin: auto;
      background-image: none !important;
      background-color: transparent;
      border-radius: 50%;
    }
  }

  .custom-control-input:checked + .custom-control-label {
    &::before {
      border-color: $danger;
      background-color: $white !important;
    }

    &::after {
      background-color: $danger;
    }
  }

  .custom-control-input:active + .custom-control-label {
    &::before {
      background-color: $white !important;
    }

    &::after {
      background-color: $danger;
      opacity: .3;
    }
  }

  &.align-middle {
    .custom-control-label {
      &::before {
        top: 0;
        bottom: 0;
        margin: auto;
      }

      &::after {
        top: 0;
        bottom: 0;
      }
    }
  }
}


