
.shop-figure {
  position: relative;
  height: 235px;
  margin-top: -66px;
  overflow: hidden;
}
.shop-figure::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}
.shop-figure .container {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
}
.shop-figure img {
    width: 100%;
    height: auto;
    min-height: 100%;
}
.shop-figure aside {
    border-top-left-radius: 15px;
}
.shop-menu li {
  line-height: 1.5;
}
.shop-menu .text-muted {
  opacity: .4;
}



